// Bold
@font-face {
  font-family: 'Aktiv Grotesk';
  src: url('/seed/fonts/webfonts/aktiv/AktivGrotesk_W_Bd.eot'); /* IE9 Compat Modes */
  src: url('/seed/fonts/webfonts/aktiv/AktivGrotesk_W_Bd.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
       url('/seed/fonts/webfonts/aktiv/AktivGrotesk_W_Bd.woff2') format('woff2'), /* Super Modern Browsers */
       url('/seed/fonts/webfonts/aktiv/AktivGrotesk_W_Bd.woff') format('woff');/* Pretty Modern Browsers */
  font-weight:  700;
  font-style:   normal;
  font-stretch: normal;
  unicode-range: U+000-5FF;
  font-display: fallback;
}

